
import Vue from 'vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import province from '@/api/province'
import * as base from '@/api/base'
Vue.use(AjaxPlugin)
Vue.use(ToastPlugin)
export default {
name: 'certification',
components: {
XHeader, XInput, Cell, Group, XButton, AlertModule, Alert, Flexbox, FlexboxItem, PopupPicker, Picker
},
methods: {
changeProvince (value) {
this.choicePro = value
},
authSfz () {
Vue.http.post('/api/v1/agents/realauthrealcity', {
city: this.choicePro[1],
province: this.choicePro[0]
}).then(response => {
let data = response.data
let msg = data.msg || data.error || '请求失败'
if (data.code !== 0) {
this.$vux.toast.text(msg, 'middle')
return false
}
base.setUserInfo('user_province', this.choicePro[0])
base.setUserInfo('user_city', this.choicePro[1])
Vue.http.post('/api/v1/agents/realauthrealname', {
realname: this.realname,
identity: this.identity
}).then(response => {
let data = response.data
let msg = data.msg || data.error || '请求失败'
if (data.code !== 0) {
this.$vux.toast.text(msg, 'middle')
return false
}
base.setUserInfo('user_auth', 2)
base.setUserInfo('user_realname', this.realname)
base.setUserInfo('user_identityCode', this.identity)
this.$vux.toast.text('提交成功', 'middle')
this.$router.push('/authChangeIdCard')
})
})
},
submitSfz () {
Vue.http.post('/api/v1/agents/realauthrealname', {
realname: this.realname,
identity: this.identity.toString()
}).then(response => {
if (response.data.code !== 0) {
base.setUserInfo('user_realname', this.realname)
base.setUserInfo('user_addrCode', this.addrCode)
}
})
},
submitPro (successCb) {}
},
data () {
return {
choicePro: [],
province: province,
realname: '',
addrCode: '',
submitStatus: 0,
identity: ''
}
}
}



